<template>
	<div id="goods-comment">
		<public-header title="商品评价"></public-header>
		<div class="content">
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<div class="search-wrap van-hairline--bottom">
					<div class="search-item" :class="{active: !filter.thumb_status && !filter.activity}" @click="onToggleFilter('', 0)">
						<span>全部</span>
						<span>{{total}}</span>
					</div>
					<div class="search-item" :class="{active: filter.thumb_status == 1}" @click="onToggleFilter('thumb_status', 1)" v-if="total_has_thumb > 0">
						<span>有图</span>
						<span>{{total_has_thumb}}</span>
					</div>
					<div class="search-item" :class="{active: filter.activity == 1}" @click="onToggleFilter('activity', 1)" v-if="total_activity_good > 0">
						<span class="icon icon-appreciate_fill_light"></span>
						<span>{{total_activity_good}}</span>
					</div>
					<div class="search-item" :class="{active: filter.activity == 2}" @click="onToggleFilter('activity', 2)" v-if="total_activity_bad > 0">
						<span class="icon icon-appreciate_fill_light rotate"></span>
						<span>{{total_activity_bad}}</span>
					</div>
				</div>
				<div class="padding-10-tb padding-15-lr font-12 bg-default margin-10-b">
					<van-checkbox v-model="filter.note" icon-size="16px" @click="onChangeNote" checked-color="#ff2d4b">只看有内容评价</van-checkbox>
				</div>
				<van-list
						v-model="records.loading"
						:finished="records.finished"
						@load="onLoad"
						:offset="100"
						:immediate-check="false"
						v-if="!records.empty"
				>
					<div class="goods-comment">
						<div class="comment-item" v-for="(item, index) in records.data" :key="index">
							<div class="comment-inner border-1px-b">
								<div class="left">
									<div class="avatar ">
										<img :src="item.avatar" />
									</div>
								</div>
								<div class="right">
									<van-row>
										<van-col span="17">
											<div class="w-100 ellipsis margin-5-t">{{item.username}}</div>
											<div class="flex margin-5-t" v-if="item.activity > 0">
												<span class="icon icon-appreciate_fill_light c-danger" :class="{rotate: item.activity == 2}"></span>
												<span class="font-12 c-gray">{{item.activity == 1 ? '赞' : '踩'}}了该商品</span>
											</div>
										</van-col>
										<van-col span="7" class="text-right font-12 c-gray">
											{{item.addtime_cn}}
										</van-col>
									</van-row>
									<div class="note" v-if="item.note">{{item.note}}</div>
									<div class="thumbs" v-if="item.thumbs && item.thumbs.length > 0">
										<van-row>
											<van-col span="6" v-for="(thumbItem, thumbIndex) in item.thumbs" :key="thumbIndex">
												<div class="thumb-item" @click="util.jsPreviewImage(item.thumbs, thumbIndex)">
													<div class="img-wrap">
														<img :src="thumbItem" />
													</div>
												</div>
											</van-col>
										</van-row>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="loaded-group">
						<div class="loaded" v-if="records.finished">
							<div class="loaded-tips">没有更多了</div>
						</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/goods_no_con.png" alt="">
					<p>暂无商品评价~</p>
				</div>
			</van-pull-refresh>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				goods_id: 0,
				records: {
					page: 1,
					psize: 15,
					loading: true,
					finished: false,
					empty: false,
					data:[]
				},
				total: 0,
				total_has_thumb: 0,
				total_activity_good: 0,
				total_activity_bad: 0,
				filter: {
					thumb_status: 0,
					activity: 0,
					note: false,
				},
				showPreLoading: true,
				isRefresh: false
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onToggleFilter(type, value) {
				var that = this;
				if(type == 'thumb_status') {
					this.filter.thumb_status = value;
					this.filter.activity = 0;
				} else if(type == 'activity') {
					this.filter.activity = value;
					this.filter.thumb_status = 0;
				} else {
					this.filter.activity = 0;
					this.filter.thumb_status = 0;
				}
				that.onLoad(true);
			},
			onChangeNote() {
				this.onLoad(true);
			},
			onLoad(force){
				var that = this;
				if(force === true) {
					that.records = {
						page: 1,
						psize: 15,
						loading: true,
						finished: false,
						empty: false,
						data:[]
					};
				}
				var params = that.filter;
				params.note = params.note ? 1 : 0;
				params.goods_id = that.goods_id;
				params.page = that.records.page;
				params.psize = that.records.psize;
				getRecords({
					vue: that,
					url: 'wmall/store/goods/goodsComment',
					data: params,
					success: (res) => {
						that.total = res.total;
						that.total_has_thumb = res.total_has_thumb;
						that.total_activity_good = res.total_activity_good;
						that.total_activity_bad = res.total_activity_bad;
					}
				})
			},
			onPullDownRefresh() {
				this.onLoad(true);
			}
		},
		mounted(){
			var query = this.util.parseQuery(this.$route.query);
			this.goods_id = query.goods_id;
			this.filter.thumb_status = query.thumb_status;
			this.filter.activity = query.activity;
			this.onLoad();
		}
	}
</script>

<style>
	#goods-comment .search-wrap{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		padding: 10px 15px;
		background-color: #fff;
	}
	#goods-comment .search-wrap .search-item{
		display: flex;
		align-items: center;
		padding: 5px 10px;
		margin-right: 10px;
		background-color: #f5f5f5;
		border-radius: 5px;
		color: #ccc;
		font-size: 12px;
	}
	#goods-comment .search-wrap .search-item.active{
		background-color: #ff2d4b;
		color: #fff;
	}
	#goods-comment .icon.rotate{
		transform: rotateX(180deg);
	}
	#goods-comment .goods-comment .comment-item{
		padding-left: 15px;
		background-color: #fff;
	}
	#goods-comment .goods-comment .comment-item .comment-inner{
		padding: 15px 15px 15px 0;
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
	#goods-comment .goods-comment .comment-item .comment-inner .left{
		width: 12%;
	}
	#goods-comment .goods-comment .comment-item .comment-inner .avatar{
		position: relative;
		width: 100%;
		padding-top: 100%;
		border-radius: 200px;
		overflow: hidden;
	}
	#goods-comment .goods-comment .comment-item .comment-inner .avatar img{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	#goods-comment .goods-comment .comment-item .comment-inner .right{
		width: 88%;
		padding-left: 10px;
		font-size: 14px;
	}
	#goods-comment .goods-comment .comment-item .comment-inner .note{
		margin: 5px 0;
		line-height: 1.5;
	}
	#goods-comment .goods-comment .comment-item .thumbs{
		margin: 5px -5px -5px -5px;
	}
	#goods-comment .goods-comment .comment-item .thumb-item{
		width: 100%;
		padding-top: 100%;
		position: relative;
	}
	#goods-comment .goods-comment .comment-item .thumb-item .img-wrap{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		padding: 5px;
		border-radius: 5px;
		overflow: hidden;
	}
	#goods-comment .goods-comment .comment-item .thumb-item .img-wrap img{
		width: 100%;
		height: 100%;
		border-radius: 3px;
	}
	#goods-comment .goods-comment .comment-total{
		margin: 10px 15px;
		padding: 15px;
		text-align: center;
		font-size: 14px;
		background-color: #fff;
		border-radius: 5px;
	}
</style>


